import React from 'react';

import {Card, CardTitle, CardText} from 'material-ui/Card';
import {red500, grey800, fullWhite, indigo500} from 'material-ui/styles/colors'
import FontIcon from 'material-ui/FontIcon';

import {Link} from 'react-router';

import Axios from 'axios';

import Cookies from 'js-cookie';

const github_api = 'https://api.github.com/users/';


class GithubUser extends React.Component{

	constructor(props){
		super(props);

		this.state = {
            username: this.props.username,
			userinfo: '',
			// userinfo_follower: '',
			// userinfo_following: '',
			// userinfo_starred: '',
			// userinfo_repos:'',
		};
	}

	componentDidMount() {
        var _this = this;
        if(this.state.username === null || this.state.username === ''){
            console.log("Username not found");
            return;
        }
        var username = this.state.username;

        const userinfo = github_api+username;
        // const userinfo_follower = github_api+username+'/followers';
        // const userinfo_following = github_api+username+'/following';
        // const userinfo_starred = github_api+username+'/starred';
        // const userinfo_repos = github_api+username+'/repos?type=all';

        Axios.get(userinfo)
            .then(function(response){
                console.log(response.data);
                response.data.compact_info = response.data.login+" @ "+response.data.location;
                _this.setState({
                	userinfo: response.data,
                });
              //   Axios.get(userinfo_repos+'&per_page='+(response.data.public_repos+2))
		            // .then(function(response){
		            //     console.log(response.data);
		            //     _this.setState({
		            //     	userinfo_repos: response.data,
		            //     });
		            // });
            });
        // Axios.get(userinfo_follower)
        //     .then(function(response){
        //         console.log(response.data);
        //         _this.setState({
        //         	userinfo_follower: response.data,
        //         });
        //     });
        // Axios.get(userinfo_following)
        //     .then(function(response){
        //         console.log(response.data);
        //         _this.setState({
        //         	userinfo_following: response.data,
        //         });
        //     });
        // Axios.get(userinfo_starred)
        //     .then(function(response){
        //         console.log(response.data);
        //         _this.setState({
        //         	userinfo_starred: response.data,
        //         });
        //     });
    }

	render(){
        const styles = {
            cardContainer: {
                display: 'flex',
                flexDirection:'row',
                textAlign: 'center',
                alignItems:'center',
                flexWrap:'wrap',
                justifyContent:'center',
            },
            card: {
                paddingBottom:0,
                width: 400,
                minHeight: 400,
                margin:5, 
            },
            card2: {
                width: 600,
                margin:5, 
            },
            card3: {
                width: 300,
                margin:5, 
            },
            cardText: {
                paddingBottom:0,
                display: 'flex',
                flexDirection:'column',
                textAlign: 'center',
                alignItems:'center',
                flexWrap:'wrap',
                justifyContent:'center',
            },
            cardTextWithPadding: {
                display: 'flex',
                flexDirection:'column',
                textAlign: 'center',
                alignItems:'center',
                flexWrap:'wrap',
                justifyContent:'center',
            },
            title: {
                fontSize: '25px',
            },
            subTitle: {
                fontSize: '18px',
            },
            icon:{
                fontSize: '30px',
                margin: '16px',
            }, 
            linkColor: {
                color: indigo500, 
                textDecoration: 'none',
            },
        };
		return (
            <div>
                {
                    this.state.userinfo !== null || this.state.userinfo !== '' || this.state.userinfo!==undefined?
                        <div style={styles.cardContainer}>
                            <Card style={styles.card}>
                                <CardText style={styles.cardText}>
                                    <img src={this.state.userinfo.avatar_url} style={{width:300, height:300,borderRadius:'50%',}} alt={this.state.userinfo.login} />
                                    <CardTitle title={this.state.userinfo.name} subtitle={this.state.userinfo.compact_info} />
                                </CardText>
                            </Card>
                            <Card style={styles.card2}>
                                <CardText style={styles.cardTextWithPadding}>
                                    <p>
                                        Hello, my name is <span style={styles.title}>Muhammad Abdusy Syukur</span> a.k.a. <span style={styles.title}>kido1611</span>. 
                                        I'm interest in <span style={styles.subTitle}>Android Develompent</span>, <span style={styles.subTitle}>Web Development</span>, and <span style={styles.subTitle}>Application Programming</span>.
                                    </p>
                                    <p>
                                    Now my project is learning <a href="http://opencv.org" target="_blank" style={styles.linkColor}>OpenCV</a>, <a href="https://facebook.github.io/react/" target="_blank" style={styles.linkColor}>ReactJS</a>, <a href="http://angularjs.org" target="_blank" style={styles.linkColor}>AngularJS</a>.
                                    </p>
                                </CardText>
                            </Card>
                            <Card style={styles.card3}>
                                <CardText style={styles.cardTextWithPadding}>
                                    <CardTitle title={'Contact me'} />
                                    <div>
                                        <Link href="http://facebook.com/muhammad.as16" target="_blank">
                                            <FontIcon style={styles.icon} color={'#3b5998'} className="fa fa-facebook" href="http://facebook.com/muhammad.as16" target="_blank"/>
                                        </Link>
                                        <Link href="http://github.com/kido1611" target="_blank">
                                            <FontIcon style={styles.icon} color={Cookies.get('theme')==='light'?grey800:fullWhite} className="fa fa-github fa-3x" href="http://github.com/kido1611" target="_blank"/>
                                        </Link>
                                        <Link href="mailto:ahmadci3@gmail.com" target="_blank">
                                            <FontIcon style={styles.icon} color={red500} className="fa fa-envelope-o fa-3x" href="mailto:ahmadci3@gmail.com" target="_blank"/>
                                        </Link>                                        
                                    </div>
                                </CardText>
                            </Card>
                        </div>
                    :
                        ''
                }
            </div>
		);
	}
};

export default GithubUser;



