import React, {Component, PropTypes} from 'react';

import Drawer from 'material-ui/Drawer';
import {List, ListItem, makeSelectable} from 'material-ui/List';
import Divider from 'material-ui/Divider';
import {spacing, typography, zIndex} from 'material-ui/styles';
import {indigo500} from 'material-ui/styles/colors';

import Axios from 'axios';

const SelectableList = makeSelectable(List);

const styles = {
    logo: {
        cursor: 'pointer',
        fontSize: 24,
        color: typography.textFullWhite,
        lineHeight: `${spacing.desktopKeylineIncrement}px`,
        fontWeight: typography.fontWeightLight,
        backgroundColor: indigo500,
        paddingLeft: spacing.desktopGutter,
        marginBottom: 8,
    },
    footerContainer: {
        display: 'flex',
        flexDirection:'column',
        textAlign: 'center',
        alignItems:'center',
        flexWrap:'wrap',
        justifyContent:'center',
        padding: 16,
    },
    footer: {
        fontSize: '20px',
    },
};

class AppNavDrawer extends Component {

    constructor(props){
        super(props);
        this.state = {
            projectItems: [{name2:'Loading ...'}]
        };
    }

    static propTypes = {
        docked: PropTypes.bool.isRequired,
        location: PropTypes.object.isRequired,
        onChangeList: PropTypes.func.isRequired,
        onRequestChangeNavDrawer: PropTypes.func.isRequired,
        open: PropTypes.bool.isRequired,
        style: PropTypes.object,
    };

    static contextTypes = {
        muiTheme: PropTypes.object.isRequired,
        router: PropTypes.object.isRequired,
    };

    state = {
        muiVersions: [],
    };

    componentDidMount() {
        var _this = this;
        Axios.get('https://api.github.com/users/kido1611/repos?sort=updated&type=all&per_page=200&direction=desc')
            .then(function(response){
                _this.setState(() => ({
                    projectItems: [],
                }));

                for( var i=0;i<response.data.length;i++) {
                    if(!response.data[i].fork && response.data[i].owner.login==="kido1611"){
                        if(response.data[i].name.length>24){
                            // console.log(response.data[i].name.match(/.{1,20}/g));
                            response.data[i].name2 = response.data[i].name.match(/.{1,19}/g)[0]+". . .";
                        }else{
                            response.data[i].name2 = response.data[i].name;
                        }

                        _this.setState((prevState) => ({
                            projectItems: prevState.projectItems.concat(response.data[i]),
                        }));
                    }
                }
            });
    }

    handleRequestChangeLink = (event, value) => {
        window.location = value;
    };

    handleTouchTapHeader = () => {
        this.context.router.push('/');
        this.props.onRequestChangeNavDrawer(false);
    };

    render() {
        const {
            location,
            docked,
            onRequestChangeNavDrawer,
            onChangeList,
            open,
            style,
        } = this.props;
        var _this = this;

        return (
            <Drawer
                style={style}
                docked={docked}
                open={open}
                onRequestChange={onRequestChangeNavDrawer}
                containerStyle={{zIndex: zIndex.drawer - 100}}
                >
                <div style={styles.logo} onTouchTap={this.handleTouchTapHeader}>
                    Kido1611
                </div>
                <SelectableList
                    value={location.pathname}
                    onChange={onChangeList}
                    >
                    <ListItem primaryText="Home" value="/" />
                    <ListItem primaryText="About" value="/about" />
                    <ListItem primaryText="Experiences" value="/experiences" />
                    <ListItem
                        primaryText="Github Projects"
                        primaryTogglesNestedList={true}
                        value="/projects"
                        // initiallyOpen={true}
                        nestedItems={
                            this.state.projectItems.map(item => (
                              <ListItem primaryText={item.name2} secondaryText={item.language} href={item.html_url} target="_blank" key={item.id} />
                            ))
                        }
                    />
                </SelectableList>
                <Divider />
                <div style={styles.footerContainer}>
                    <span style={styles.footer}>&copy; 2016 kido1611</span>
                </div>
            </Drawer>
        );
    }
}

// <ListItem
                        // primaryText="Tools"
                    //     primaryTogglesNestedList={true}
                    //     initiallyOpen={true}
                    //     nestedItems={[
                    //         <ListItem primaryText="Markdown Editor" value="/tools/markdowneditor" />,
                    //         <ListItem primaryText="SVG to Vector Drawable" value="/tools/svgtovector" />,
                    //         <ListItem primaryText="Base64" value="/tools/base64" />,
                    //         <ListItem primaryText="HTML Editor" value="/tools/htmleditor" />,
                    //         <ListItem primaryText="Github User Info" value="/tools/githubuser" />,
                    //     ]}
                    // />

export default AppNavDrawer;



