import React , {Component, PropTypes} from 'react';
import Title from 'react-title-component';

import AppBar from 'material-ui/AppBar';
import IconMenu from 'material-ui/IconMenu';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import {indigo500, fullWhite} from 'material-ui/styles/colors';
import spacing from 'material-ui/styles/spacing';
import AppNavDrawer from './components/AppNavDrawer';
import withWidth, {MEDIUM, LARGE} from 'material-ui/utils/withWidth';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import IconButton from 'material-ui/IconButton';
import MenuItem from 'material-ui/MenuItem';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import Paper from 'material-ui/Paper';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/communication/comment';

import darkBaseTheme from './styles/darkBaseTheme';
import lightBaseTheme from './styles/lightBaseTheme';

import Cookies from 'js-cookie';

class Base extends Component{

	constructor(props){
		super(props);
		this.state = {
			darkTheme: false, 
			navDrawerOpen: false, 
			openDialog: false, 
			docked: false,
			showMenuIconButton: true,
			openDialogMimin: false,
		};
	}

	static propTypes = {
		children: PropTypes.node,
		location: PropTypes.object,
		width: PropTypes.number.isRequired,
	};

	static contextTypes = {
		router: PropTypes.object.isRequired,
	};

	static childContextTypes = {
		muiTheme: PropTypes.object,
	};

	getChildContext() {
		return {
			muiTheme: this.state.muiTheme,
		};
	}

	componentWillMount() {
		this.setState({
			darkTheme:Cookies.get('theme')==='light'?false:true,
			muiTheme: getMuiTheme(Cookies.get('theme')==='dark'?darkBaseTheme:lightBaseTheme),
		});
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const newMuiTheme = nextContext.muiTheme ? nextContext.muiTheme : this.state.muiTheme;
		this.setState({
			muiTheme: newMuiTheme,
		});
	}

	getStyles() {
		const styles = {
			appBar: {
				position: 'fixed',
				zIndex: this.state.muiTheme.zIndex.appBar + 1,
				top: 0,
				backgroundColor: indigo500,
			},
			container: {
				height: '100%',
				minHeight: '100vh',
				minWidth:400,
			},
			root: {
				paddingTop: spacing.desktopKeylineIncrement,
				minHeight: 400,
				fontFamily: 'Roboto, sans-serif',
			},
			content: {
				margin: spacing.desktopGutter,
			},
			contentWhenMedium: {
				margin: `${spacing.desktopGutter * 2}px ${spacing.desktopGutter * 3}px`,
			},
			fab_message: {
				position: 'fixed',
				bottom: 32, 
				right: 32, 
				zIndex: 1000,
			},
		};

		if (this.props.width === MEDIUM || this.props.width === LARGE) {
			styles.content = Object.assign(styles.content, styles.contentWhenMedium);
		}

		return styles;
	}

	handleTouchTapLeftIconButton = () => {
		this.setState({
			navDrawerOpen: !this.state.navDrawerOpen,
		});
	};

	handleChangeRequestNavDrawer = (open) => {
		this.setState({
			navDrawerOpen: open,
		});
	};

	handleChangeList = (event, value) => {
		this.context.router.push(value);
		this.setState({
			navDrawerOpen: false,
		});
	};

	handleChangeMuiTheme = (muiTheme) => {
		this.setState({
			muiTheme: muiTheme,
		});
	};

	changeTheme = () => {
		if(this.state.darkTheme){
			this.handleChangeMuiTheme(getMuiTheme(lightBaseTheme));
			this.setState({
				darkTheme: false
			});
			document.cookie = "theme=light";
			Cookies.set('theme', 'light');
		}else{
			this.handleChangeMuiTheme(getMuiTheme(darkBaseTheme));
			this.setState({
				darkTheme: true
			});
			Cookies.set('theme', 'dark');
		}
		this.setState({openDialog: false,});
	};

	openDialog = () => {
		this.setState({openDialog: !this.state.openDialog});
	};

	openDialogMimin = () => {
		this.setState({openDialogMimin: !this.state.openDialogMimin});
	};

	render(){
		const {
			location,
			children,
		} = this.props;

		let {
			navDrawerOpen,
			docked,
			showMenuIconButton,
		} = this.state;

		const {
			prepareStyles,
		} = this.state.muiTheme;

		const router = this.context.router;
	    const styles = this.getStyles();
	    const title =
			router.isActive('/home') ? 'Home' :
			router.isActive('/about') ? 'About' :
			router.isActive('/projects/') ? 'Project Lists' :
			router.isActive('/tools/') ? 'Tools' : '';

		if (this.props.width === LARGE || this.props.width === MEDIUM) {

			if(this.props.location.pathname === '/'){
				docked = false;
			}else{
				docked = true;
				navDrawerOpen = true;
				showMenuIconButton = false;

				styles.navDrawer = {
					zIndex: styles.appBar.zIndex - 1,
				};
				styles.root.paddingLeft = 256;
			}
		}else{
			showMenuIconButton = true;			
		}

		const actionsFeedback = [
				<FlatButton
					label="Cancel"
					primary={true}
					onTouchTap={this.openDialog}
				/>,
				<FlatButton
					label="Submit"
					primary={true}
					onTouchTap={this.changeTheme}
					keyboardFocused={true}
				/>,
			];

		const actionsMimin = [
				<FlatButton
					label="Cancel"
					primary={true}
					onTouchTap={this.openDialogMimin}
				/>,
				<FlatButton
					label="Login"
					primary={true}
					onTouchTap={this.changeTheme}
					keyboardFocused={true}
				/>,
			];

		return (
			<Paper zDepth={0} style={styles.container}>
				<Title render="kido1611" />
				<AppBar
					onLeftIconButtonTouchTap={this.handleTouchTapLeftIconButton}
					title={title}
					zDepth={0}
					style={styles.appBar}
					showMenuIconButton={showMenuIconButton}
					iconElementRight={
						<IconMenu iconButtonElement={<IconButton ><MoreVertIcon /></IconButton>}>
							<MenuItem primaryText="Change Theme" onClick={this.changeTheme}/>
							<MenuItem primaryText="Mimin" onClick={this.openDialogMimin} />
						</IconMenu>
					}
					/>
				{
					this.props.location.pathname !== '/' ? 
						<div style={prepareStyles(styles.root)}>
							<div style={prepareStyles(styles.content)}>
								{React.cloneElement(children, {
									onChangeMuiTheme: this.handleChangeMuiTheme,
								})}
							</div>
						</div>
					:
						children
				}
				<AppNavDrawer
					style={styles.navDrawer}
					location={location}
					docked={docked}
					onRequestChangeNavDrawer={this.handleChangeRequestNavDrawer}
					onChangeList={this.handleChangeList}
					open={navDrawerOpen}
					/>
				<Dialog
					title="Feedback for me"
					actions={actionsFeedback}
					modal={true}
					open={this.state.openDialog}
					onRequestClose={this.openDialog}
					>
					Under construction.
				</Dialog>
				<Dialog
					title="Mimin"
					actions={actionsMimin}
					modal={true}
					open={this.state.openDialogMimin}
					onRequestClose={this.openDialogMimin}
					>
					Under construction.
				</Dialog>
				<FloatingActionButton secondary={true} style={styles.fab_message} onClick={this.openDialog.bind(this)}>
					<ContentAdd color={fullWhite}/>
				</FloatingActionButton>
			</Paper>
		);
	}
}

export default withWidth()(Base);



