import React, {PropTypes} from 'react';
import Title from 'react-title-component';
import {indigo500} from 'material-ui/styles/colors'
import Typist from 'react-typist';
import withWidth from 'material-ui/utils/withWidth';
import '../styles/Typist.css';

class Home extends React.Component{

	constructor(props){
		super(props);
		this.state = {
			message1: true,
			message2: false,
		};
	}

	static propTypes = {
		docked : PropTypes.bool,
		navDrawerOpen : PropTypes.bool,
		showMenuIconButton : PropTypes.bool,
	};

	startMessage2 = () => {
		this.setState({
			message2: true,
		});
	};

	delayGen(mean, std, { line, lineIdx, charIdx, defDelayGenerator }) {
	if (lineIdx === 0 && charIdx === line.length - 1) {
	return 1250;
	}
	if (lineIdx === 1 && charIdx === line.length - 1) {
	return 1250;
	}
	if (lineIdx === 3 && charIdx === line.length - 1) {
	return 1250;
	}
	return defDelayGenerator(mean + 25);
	}

	render(){

		const styles = {
			section1: {
				background:indigo500,
				height:'100vh',
				display:'flex',
				flexDirection:'column',
				textAlign: 'center',
				alignItems:'center',
				flexWrap:'wrap',
				justifyContent:'center',
				textColor:'#ffffff',
				color:'#ffffff',
			},
			title:{
				fontSize: 60,
			},
			subtitle: {
				fontSize: 20,
			},
		};

		return (
			<div>
				<Title render={(previousTitle) => `Home - ${previousTitle}`} />
				<section style={styles.section1}>
					<Typist startDelay={2000} avgTypingSpeed={1500} stdTypingDelay={1} onTypingDone={this.startMessage2} delayGenerator={this.delayGen} cursor={{hideWhenDone: true, hideWhenDoneDelay:10}}>
						<span style={styles.title}>Hello world</span>
					</Typist>
					<br /> 
					<br /> 
					<br /> 
					<br /> 
					<br /> 
					<br /> 
					<br /> 
					{
						this.state.message2 ?
							<Typist startDelay={3000} delayGenerator={this.delayGen}>
								<span style={styles.subtitle}>Are you ready...?</span>
							</Typist>
						:
							null
					}
				</section>
			</div>
		);
	}	
}

export default withWidth()(Home);;



