import React from 'react';
import {
  Route,
  IndexRoute,
} from 'react-router';

import BasePage from './Base';
import HomePage from './page/Home';
import AboutPage from './page/About';
import ExperiencePage from './page/Experience';
// import MarkdownViewer from './page/MarkdownViewer';
// import SVGToVector from './page/SVGToVector';
// import GithubUserInfo from './page/GithubUserInfo';
// <Route path="tools/markdowneditor" component={MarkdownViewer} />
// <Route path="tools/svgtovector" component={SVGToVector} />
// <Route path="tools/githubuser" component={GithubUserInfo} />
// <Route path="tools/githubuser/:username" component={GithubUserInfo} />

import Error404 from './page/error/Error404';

const IndexRoutes = (
	<Route path="/" component={BasePage}>
		<IndexRoute component={HomePage} />
		<Route path="home" component={HomePage} />
		<Route path="about" component={AboutPage} />
		<Route path="experiences" component={ExperiencePage} />
		<Route path="*" component={Error404}/>
	</Route>
);

export default IndexRoutes;



