import React from 'react';
import Title from 'react-title-component';

class Experience extends React.Component{

	render(){
		return (
			<div>
				<Title render={(previousTitle) => `Experiences - ${previousTitle}`} />
				<h1>Experiences</h1>
				
			</div>		
		);
	}
};

export default Experience;



